import React from 'react';

class UserForm extends React.Component
{
    state = { firstName: '', lastName: '', country: '', text: '' }

    onFormSubmitted = (e) =>
    {
        e.preventDefault()

        this.props.onUserFormSubmit(this.state)

        this.setState({ firstName: '', lastName: '', country: '', text: '' })
    }

    render()
    {
        return (
            <div className="row">
                <div className="container">
                    <form action="/action_page.php">
                        <label htmlFor="fname" >First Name</label>
                        <input  onChange={e => this.setState({ firstName: e.target.value })} value={this.state.firstName} type="text" id="fname" name="firstname" placeholder="Your name.." />

                        <label htmlFor="lname">Last Name</label>
                        <input onChange={e => this.setState({ lastName: e.target.value })} value={this.state.lastName} type="text" id="lname" name="lastname" placeholder="Your last name.." />

                        <label htmlFor="country">Country</label>
                        <select onChange={e => this.setState({ country: e.target.value })} value={ this.state.country } id="country" name="country">
                        <option >Australia</option>
                        <option >Canada</option>
                        <option >USA</option>
                        <option >Italy</option>
                        <option >Norway</option>
                        <option >Denmark</option>
                        <option >China</option>
                        <option >Bill Nye the Science Guy</option>
                        </select>

                        <label htmlFor="subject">Subject</label>
                        <textarea onChange={e => this.setState({ text: e.target.value })} value={this.state.text} id="subject" name="subject" placeholder="Write something.." style={{height: '200px'}} />

                        <input  onClick={this.onFormSubmitted} type="submit" value="submit" />
                    </form>
                </div>
            </div>
        );
    };
}

export default UserForm;