import React from 'react';

class Search extends React.Component {

    onButtonClicked = (e) =>
    {
        e.preventDefault()
        this.props.onSearchButtonClicked()
    }


    render()
    {
        return (
        <a href="/Search" onClick={this.onButtonClicked}>
            Search
        </a>
        );
    };
}

export default Search;