import React from 'react';
import SearchBar from './VideoSearchPage/SearchBar';
import './css.css';
import youtube from '../apis/youtube';
import VideoList from './VideoSearchPage/VideoList';
import VideList from './VideoSearchPage/VideoList';
import Contact from './NavBar/Contact';
import Search from './NavBar/Search';
import Home from './NavBar/Home';
import ContactPage from './ContactPage/ContactPage';
import VideoDetail from './VideoSearchPage/VideoDetail';

class App extends React.Component
{
    state = { videos: [], selectedVideo: null, searchDisplay: 'd-none', homeDisplay: '', contactDisplay: 'd-none' }

    componentDidMount()
    {
        this.onTermSubmit('cars')
    }

    onTermSubmit = async (term) =>
    {
        const response = await youtube.get('/search', {
            params: {
                q: term
            }
        });
        this.setState({ videos: response.data.items, selectedVideo: response.data.items[0] })
    }

    onVideoSelect = (video) =>
    {
        this.setState({ selectedVideo: video })
    }

    onHomeButtonClicked = () =>
    {
        this.setState({searchDisplay: 'd-none', homeDisplay: '', contactDisplay: 'd-none'})
    }

    onSearchButtonClicked = () =>
    {
        this.setState({searchDisplay: '', homeDisplay: 'd-none', contactDisplay: 'd-none'})
    }

    onContactButtonClicked = () =>
    {
        this.setState({searchDisplay: 'd-none', homeDisplay: 'd-none', contactDisplay: ''})
    }

    render()
    {
        return (
            <div className="container background" >
                <div>
                    <nav>
                        <ul>
                            <li><Contact onContactButtonClicked={this.onContactButtonClicked} /></li>
                            <li><Search onSearchButtonClicked={this.onSearchButtonClicked} /></li>
                            <li><Home onHomeButtonClicked={this.onHomeButtonClicked} /></li>
                        </ul>
                    </nav>
                </div>
                <div className={`body ${this.state.searchDisplay}`} >
                    <SearchBar onFormSubmit={this.onTermSubmit} />
                    <div className="main">
                        <VideoDetail video={this.state.selectedVideo} />
                    </div>
                    <div className="menu" >
                        <VideList onVideoSelect={this.onVideoSelect} videos={ this.state.videos } />
                    </div>
                </div>
                <div className={`body ${this.state.homeDisplay}`} >
                    <nav className="form" >
                        <h1>Home Display</h1>
                    </nav>
                    <div className="row">
                        <div className="col main menu-title border">
                            <h1>Main</h1>
                        </div>
                        <div className="col menu-title border">
                            <h1>Menu</h1>
                        </div>
                    </div>
                </div>
                <div className={`body ${this.state.contactDisplay}`} >
                    <ContactPage />
                </div>
            </div>
        )
    };
}

export default App;