import React from 'react';

class Contact extends React.Component {

    onButtonClicked = (e) =>
    {
        e.preventDefault()
        this.props.onContactButtonClicked()
    }

    render()
    {
        return (
        <a href="/Contact" onClick={this.onButtonClicked} >
            Contact
        </a>
        );
    }
};

export default Contact;