import React from 'react';

class Home extends React.Component {
    
    onHomeClicked = (e) =>
    {
        e.preventDefault()
        this.props.onHomeButtonClicked()
    }
    
    render()
    {
        return (
            <a href="/Home" onClick={this.onHomeClicked}>
                Home
            </a>
        );
    };
}

export default Home;